<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 原因代码 -->
            <a-form-model-item
              label="原因代码"
              prop="reasonCode"
              ref="reasonCode"
            >
              <a-input
                v-model="entity.reasonCode"
                placeholder="请输入原因代码"
                name="reasonCode"
                :disabled="!!id"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 原因名称 -->
            <a-form-model-item
              label="原因名称"
              prop="reasonName"
              ref="reasonName"
            >
              <a-input
                v-model="entity.reasonName"
                placeholder="请输入原因名称"
                name="reasonName"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 关联状态码 -->
            <a-form-model-item
              label="关联状态码"
              prop="relatedStatusCode"
              ref="relatedStatusCode"
            >
              <a-select
                v-model="entity.relatedStatusCode"
                placeholder="请选择关联状态码"
                name="relatedStatusCode"
                @change="onStatusCodeChange"
              >
                <a-select-option v-for="item in statusCodeList" :key="item.code" :value="item.code">
                  {{ item.code }} - {{ item.name }}
                </a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-radio-group v-model="entity.status">
                <a-radio value="启用">启用</a-radio>
                <a-radio value="禁用">禁用</a-radio>
              </a-radio-group>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 描述 -->
            <a-form-model-item
              label="描述"
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入描述信息"
                :rows="4"
                :maxLength="1000"
                name="description"
              />
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 建议操作 -->
            <a-form-model-item
              label="建议操作"
              prop="suggestedAction"
              ref="suggestedAction"
            >
              <a-textarea
                v-model="entity.suggestedAction"
                placeholder="请输入建议操作"
                :rows="4"
                :maxLength="1000"
                name="suggestedAction"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>
      <g-collapse headerName="详细信息" v-if="id">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditResourceStatusReason",
  mixins: [GNdoPageEditBase],
  data() {
    return {
      entity: {
        reasonCode: "",
        reasonName: "",
        relatedStatusCode: undefined,
        relatedStatusName: "",
        status: "启用",
        description: "",
        suggestedAction: "",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      },
      // 状态码列表
      statusCodeList: [
        { code: "E001", name: "设备离线" },
        { code: "E002", name: "连接超时" },
        { code: "E003", name: "认证失败" },
        { code: "E004", name: "硬件故障" },
        { code: "E005", name: "软件异常" }
      ],
      rules: {
        reasonCode: [
          { required: true, message: "原因代码不能为空", trigger: "change" },
          {
            pattern: /^R[0-9]{3}$/,
            message: "原因代码格式不正确，应为R开头加3位数字",
            trigger: "change"
          }
        ],
        reasonName: [
          { required: true, message: "原因名称不能为空", trigger: "change" },
          {
            pattern: /^[^\s]+(\s+[^\s]+)*$/,
            message: "原因名称不能包含前后空格",
            trigger: "change"
          }
        ],
        relatedStatusCode: [
          { required: true, message: "关联状态码不能为空", trigger: "change" }
        ],
        status: [
          { required: true, message: "状态不能为空", trigger: "change" }
        ]
      }
    };
  },

  methods: {
    // 当选择状态码时更新关联信息
    onStatusCodeChange(value) {
      const selectedStatus = this.statusCodeList.find(item => item.code === value);
      if (selectedStatus) {
        this.entity.relatedStatusName = selectedStatus.name;
      }
    },
    
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取状态原因数据
        setTimeout(() => {
          this.loading = false;
          this.entity = {
            reasonCode: "R001",
            reasonName: "网络断开原因",
            relatedStatusCode: "E001",
            relatedStatusName: "设备离线",
            status: "启用",
            description: "由于网络断开导致的设备离线问题",
            suggestedAction: "建议检查网络连接并重启设备",
            createdBy: "admin",
            createdTime: "2024-03-18 10:00:00",
            modifiedBy: "admin",
            modifiedTime: "2024-03-18 11:55"
          };
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },

    resetFormData() {
      this.entity = {
        reasonCode: "",
        reasonName: "",
        relatedStatusCode: undefined,
        relatedStatusName: "",
        status: "启用",
        description: "",
        suggestedAction: "",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: ""
      };
    },

    async save() {
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.reasonName);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null
          });
        }, 500);
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}
</style> 